<template>
  <el-input-number v-model="num" @change="handleChange" :min="1" :max="10" :step="1" label="描述文字"></el-input-number>
</template>
<script>
export default {
  data() {
    return {
      num: 1
    };
  },

  methods: {
    handleChange(value) {
      this.$emit("updateNum", value);
    }
  }
};
</script>

